body {
  margin: auto;
  font: normal 16px / normal "Courier New", Courier, monospace;
  color: #737373;
}
body i {
  color: #737373;
}
body p {
  color: #404040;
}
body div.main {
  text-align: center;
}
body ul {
  animation: fadein 1s;
  -moz-animation: fadein 1s;
  /* Firefox */
  -webkit-animation: fadein 1s;
  /* Safari et Chrome */
  -o-animation: fadein 1s;
  /* Opera */
}
.main-container {
  height: 100vh;
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  margin: auto;
  border: none;
  overflow: hidden;
  z-index: 15;
  font: normal 16px / normal "Courier New", Courier, monospace;
  color: #737373;
  text-align: center;
  -o-text-overflow: ellipsis;
  text-overflow: ellipsis;
  background-position: 50% 50%;
  -webkit-background-origin: padding-box;
  background-origin: padding-box;
  -webkit-background-clip: border-box;
  background-clip: border-box;
  -webkit-background-size: auto auto;
  background-size: auto auto;
}
.main-container ::-webkit-input-placeholder {
  /* Chrome */
  color: #5a76b6;
  opacity: 0.4;
}
.main-container :-ms-input-placeholder {
  /* IE 10+ */
  color: #5a76b6;
}
.main-container ::-moz-placeholder {
  /* Firefox 19+ */
  color: #5a76b6;
}
.main-container :-moz-placeholder {
  /* Firefox 4 - 18 */
  color: #5a76b6;
}
.main-container .dropdown {
  animation: fadein 10s;
  -moz-animation: fadein 10s;
  /* Firefox */
  -webkit-animation: fadein 10s;
  /* Safari et Chrome */
  -o-animation: fadein 10s;
  /* Opera */
  text-align: left;
}
.main-container .dropdown .btn-default {
  background-color: rgba(255, 255, 255, 0);
  margin-top: 10px;
  border: 1px solid rgba(25, 13, 13, 0.3);
}
.main-container .dropdown .btn-default[aria-expanded~=true] {
  background-color: rgba(255, 255, 255, 0);
}
.main-container .dropdown .dropdown-menu {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-container .dropdown .dropdown-menu li > a:focus,
.main-container .dropdown .dropdown-menu li > a:hover {
  background-color: rgba(255, 255, 255, 0.3);
}
.main-container .login-container {
  animation: fadein 10s;
  -moz-animation: fadein 10s;
  /* Firefox */
  -webkit-animation: fadein 10s;
  /* Safari et Chrome */
  -o-animation: fadein 10s;
  /* Opera */
  margin-top: 10px;
  position: absolute;
  background-color: rgba(255, 255, 255, 0);
}
.main-container .login-container i {
  font-size: 1.5em;
}
.footer-container {
  position: absolute;
  bottom: 0px;
  animation: fadein 10s;
  -moz-animation: fadein 10s;
  /* Firefox */
  -webkit-animation: fadein 10s;
  /* Safari et Chrome */
  -o-animation: fadein 10s;
  /* Opera */
}
.footer-container .dropup {
  position: absolute;
  bottom: 0px;
  right: 0px;
  margin-bottom: 20px;
  margin-right: 20px;
}
.footer-container .dropup .btn-default {
  background-color: rgba(255, 255, 255, 0);
  margin-top: 10px;
  border-radius: 100%;
  border: 1px solid rgba(25, 13, 13, 0.3);
}
.footer-container .dropup .btn-default[aria-expanded~=true] {
  background-color: rgba(255, 255, 255, 0);
}
.footer-container .dropup .dropdown-menu {
  left: -160px;
  background-color: rgba(255, 255, 255, 0.3);
}
.footer-container .dropup .dropdown-menu li > a:focus,
.footer-container .dropup .dropdown-menu li > a:hover {
  background-color: rgba(255, 255, 255, 0.3);
}
.footer-container .dropup .dropdown-menu p {
  padding: 4px 15px;
}
.connexion-container {
  left: 0px;
  top: 0px;
  right: 0px;
  bottom: 0px;
  padding: 34px;
  position: fixed;
  margin: auto;
  text-align: center;
  height: 500px;
}
.connexion-container .input-group {
  position: relative;
  margin-top: 25px;
  width: 86%;
  margin-left: 4px;
}
.connexion-container .valid-connexion {
  margin-top: 35px;
  background-color: rgba(255, 255, 255, 0);
  border: 1px solid rgba(25, 13, 13, 0.3);
}
#main-title {
  font-size: 58px;
  text-align: center;
  margin-top: 20vh;
}
.input-container .input-group {
  animation: fadein 10s;
  -moz-animation: fadein 10s;
  /* Firefox */
  -webkit-animation: fadein 10s;
  /* Safari et Chrome */
  -o-animation: fadein 10s;
  /* Opera */
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 40%;
}
.input-container .form-control {
  display: inline-block;
  -webkit-box-sizing: content-box;
  -moz-box-sizing: content-box;
  box-sizing: content-box;
  padding: 10px 20px;
  border: 0 solid rgba(183, 183, 183, 0);
  border-bottom: 1px solid #737373;
  -webkit-border-radius: 3px;
  border-radius: 3px;
  font: normal 16px / normal "Courier New", Courier, monospace;
  color: #737373;
  -o-text-overflow: clip;
  text-overflow: clip;
  background: rgba(252, 252, 252, 0.03);
  -webkit-box-shadow: 0 0 0 0 #282828;
  -moz-box-shadow: 0 0 0 0 #282828;
  box-shadow: 0 0 0 0 #282828;
  text-shadow: 1px 1px 0 rgba(255, 255, 255, 0.66);
}
.input-container .form-control:hover {
  border: 0 solid #5a5a5a;
  border-bottom-width: 1px;
  -webkit-box-shadow: none;
  -moz-box-shadow: none;
  box-shadow: none;
}
.input-container .form-control:focus {
  border: 0 solid rgba(183, 183, 183, 0);
  border-bottom: 1px solid #5a5a5a;
}
.input-container .voice-activation {
  position: absolute;
  margin-left: 10px;
  margin-top: -10px;
}
#voice-activation-container {
  display: none;
}
#message-accueil {
  display: none;
}
.result-container {
  display: none;
  text-align: center;
}
.meteo-container {
  font-size: 63px;
}
.meteo-container .stage {
  top: -39px;
}
.localisation-container #start_address_result {
  font-weight: bold;
  font-size: 32px;
}
#map-container {
  height: 300px;
}
canvas {
  height: 99.53vh;
  width: 99vw;
  z-index: 150;
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-moz-keyframes fadein {
  /* sur Firefox */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes fadein {
  /* sur Safari et Chrome */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-o-keyframes fadein {
  /* sur Opera */
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
svg {
  width: 68px;
  height: 68px;
  margin: 24px 0 0 40px;
  fill: #354673;
}
/* Thunder Bolt Animation */
#Orageux .bolt {
  animation: flash 2s infinite;
}
@keyframes flash {
  0% {
    transform: translateY(-25px) translateX(25px) scaleY(0.95);
    opacity: 0;
  }
  5%,
  25% {
    opacity: 1;
  }
  15%,
  20% {
    opacity: 0;
  }
  50% {
    transform: translateY(8px) translateX(-10px);
    opacity: 1;
  }
  80% {
    transform: translateY(8px) translateX(-10px);
    opacity: 0;
  }
  100% {
    transform: translateY(-25px) translateX(25px) scaleY(0.95);
    opacity: 0;
  }
}
/* Raindrops Animation */
#pluie .raindrop-one,
#pluie .raindrop-two,
#pluie .raindrop-three {
  opacity: 0;
  animation-timing-function: cubic-bezier(1, 0, 1, 1);
}
#pluie .raindrop-one {
  animation: falling-drop-one 2s infinite;
}
#pluie .raindrop-two {
  animation: falling-drop-two 1.9s infinite;
  animation-delay: 0.8s;
}
#pluie .raindrop-three {
  animation: falling-drop-three 1.8s infinite;
  animation-delay: 0.5s;
}
@keyframes falling-drop-one {
  0% {
    transform: translateY(-35px);
    opacity: 0;
  }
  10% {
    transform: translateY(-15px);
    opacity: 1;
  }
  30% {
    opacity: 1;
  }
  60% {
    transform: translateY(100px);
    opacity: 0;
  }
  100% {
    transform: translateY(-35px);
    opacity: 0;
  }
}
@keyframes falling-drop-two {
  0% {
    transform: translateY(-105px);
    opacity: 0;
  }
  10% {
    transform: translateY(-85px);
    opacity: 1;
  }
  30% {
    opacity: 1;
  }
  60% {
    transform: translateY(5px);
    opacity: 0;
  }
  100% {
    transform: translateY(-105px);
    opacity: 0;
  }
}
@keyframes falling-drop-three {
  0% {
    transform: translateY(-105px);
    opacity: 0;
  }
  10% {
    transform: translateY(-85px);
    opacity: 1;
  }
  30% {
    opacity: 1;
  }
  60% {
    transform: translateY(35px);
    opacity: 0;
  }
  100% {
    transform: translateY(-105px);
    opacity: 0;
  }
}
/* Snowflake Animations */
#Neige .snowflake-one,
#Neige .snowflake-two,
#Neige .snowflake-three {
  opacity: 0;
  transform-origin: center center;
  animation-timing-function: ease-in;
}
#Neige .snowflake-one {
  animation: falling-snow-one 4s infinite;
}
#Neige .snowflake-two {
  animation: falling-snow-two 3.8s infinite;
  animation-delay: 2.5s;
}
#Neige .snowflake-three {
  animation: falling-snow-three 3.9s infinite;
  animation-delay: 1.5s;
}
@keyframes falling-snow-one {
  0% {
    transform: translateY(-65px) rotate(0);
    opacity: 0;
  }
  20% {
    transform: translateY(-10px) translateX(30px) rotate(-30deg);
    opacity: 1;
  }
  40% {
    transform: translateY(45px) translateX(-30px) rotate(30deg);
  }
  50% {
    opacity: 1;
  }
  60% {
    transform: translateY(100px) translateX(30px) rotate(-30deg);
    opacity: 0;
  }
  100% {
    transform: translateY(-65px);
    opacity: 0;
  }
}
@keyframes falling-snow-two {
  0% {
    transform: translateY(-75px) rotate(0);
    opacity: 0;
  }
  20% {
    transform: translateY(-45px) translateX(40px) rotate(-30deg);
    opacity: 1;
  }
  40% {
    transform: translateY(5px) translateX(-40px) rotate(30deg);
  }
  50% {
    opacity: 1;
  }
  60% {
    transform: translateY(30px) translateX(20px) rotate(-30deg);
    opacity: 0;
  }
  100% {
    transform: translateY(-75px);
    opacity: 0;
  }
}
@keyframes falling-snow-three {
  0% {
    transform: translateY(-85px) rotate(0);
    opacity: 0;
  }
  20% {
    transform: translateY(-10px) translateX(-30px) rotate(30deg);
    opacity: 1;
  }
  40% {
    transform: translateY(25px) translateX(30px) rotate(-30deg);
  }
  50% {
    opacity: 1;
  }
  60% {
    transform: translateY(60px) translateX(-30px) rotate(30deg);
    opacity: 0;
  }
  100% {
    transform: translateY(-85px);
    opacity: 0;
  }
}
/* Sunny Cloud Animations */
#Nuageux .sun-half {
  animation: sun-grow 4s infinite cubic-bezier(0.2, 0.85, 0.4, 1.5);
  transform-origin: bottom center;
}
#Nuageux .ray-one {
  animation: ray-show-one 4s infinite linear;
  transform-origin: center right;
}
#Nuageux .ray-two {
  animation: ray-show-two 4s infinite linear;
  transform-origin: bottom right;
}
#Nuageux .ray-three {
  animation: ray-show-three 4s infinite linear;
  transform-origin: bottom center;
}
#Nuageux .ray-four {
  animation: ray-show-four 4s infinite linear;
  transform-origin: bottom left;
}
#Nuageux .ray-five {
  animation: ray-show-five 4s infinite linear;
  transform-origin: center left;
}
